.tb_see_more_btn_wrap{
    text-align: center;
    width: 100%;
    padding: 15px 0;
}
.tb_see_more_btn {
    display: inline-block;
    font-weight: 400;
    color: #2b2b2b;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid #2b2b2b;
    padding: 0.5rem .5rem;
    font-size: 14px;
    line-height: 1.5;
    min-width: 110px;
    min-height: 39px;
    border-radius: 3px;
    cursor: pointer;
    margin: 0 auto;
    text-align: center;
    position: relative;
}


.tb_see_more_btn:hover, 
.tb_see_more_btn:focus, 
.tb_see_more_btn.tb_more_btn_loading{
    color: #000;
    border: 1px solid #000;
}

.tb_see_more_btn.tb_more_btn_loading{
    padding-right: 44px;
    min-width: auto;
}
.tb_more_btn_loading:before {
    content: "";
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 0;
    border-radius: 50%;
    width: 1.3rem;
    height: 1.3rem;
    margin-top: -0.6rem;
    border: 2px solid #000;
    border-right: 2px solid transparent;
    left: auto;
    right: 1rem;
    -webkit-animation: tb_btn_animation-spinner 0.5s linear infinite;
    animation: tb_btn_animation-spinner 0.5s linear infinite;
}

@-webkit-keyframes tb_btn_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  
  @keyframes tb_btn_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }