body{
    margin: 0;
    padding: 0;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.tb_app_container *,
.tb_app_container *::before,
.tb_app_container *::after {
  box-sizing: border-box;
}
.tb_app_container {
    font-family: 'Inter';
    line-height: 1.5;
    font-weight: 400;
}


.tb_app_container a{
  text-decoration: none !important;
}

.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
.tb_text_hashtag {
    display: inline;
}
@import "./scss/InterFonts.scss";

@import "./scss/IconDefaultColor.scss";

@import "./scss/SocialIcons.scss";

@import "./app/Header/scss/Header.scss";

@import "./app/Components/scss/Component.scss";

@import "./app/Themes/scss/Themes.scss";

@import "./scss/googleFonts.scss";



