.tb_ht_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_ht_post_slider{
    width: 100%;
}

.tb_ht_post_wrapper{
    width: 100%;
    padding: 7.5px;
}

.tb_ht_post_in{
    display: block;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
}

.tb_ht_post_media_wrapp{
    position: relative;
}

.tb_ht_post_details {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    z-index: 4;
    opacity: 0;
    visibility: hidden;
    background-color: rgba(0,0,0,.7);    
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.tb_ht_post_in:hover .tb_ht_post_details {
    opacity: 1;
    visibility: visible;
}

.tb_ht_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}

.tb_ht_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #f7f7f7;
}

.tb_ht_social_ {
    display: flex;
    height: 100%;
    width: 100%;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.tb_ht_social_ico_ {
    font-size: 24px;
    color: #fff;
}


.tb_ht_video_ico {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto 0;
    z-index: 1;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 40px;
    opacity: .8;
}

.tb_ht_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.4));
}

.tb_ht_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.4));
}

.tb_ht_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_ht_youtube_ico.tb-youtube:before {
    z-index: 1;
}

.tb_ht_youtube_ico.tb-youtube:after {
    content: '';
    background-color: #fff;
    width: 15px;
    height: 15px;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 0;
    display: flex;
}
.tb_ht_arrow {
    position: absolute;
    top: calc(50% - 23px);
    background-color: #000;
    border-radius: 3px;
    height: 46px;
    width: 36px;
    font-size: 20px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: #fff;
    opacity: 0.5;
    z-index: 9;
    cursor: pointer;
    transition: opacity .3s ease-in;
}

.tb_ht_arrow_right__ {
    right: 10px;
    left: auto;
}

.tb_ht_arrow_left__ {
    left: 10px;
    right: auto;
}

.tb_ht_arrow:hover {
    opacity: 0.8;
}


